.error {
    background-image: url("../../images/IngredientNotFound.webp");
    width: 100vw;
    height: 90vh;
    background-size: cover;
    background-repeat: no-repeat;
    /* background: ; */

    background-color: rgba(0, 0, 0, .5);
    background-blend-mode: multiply;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.error-h1,
.error-p {
    display: flex;
    justify-content: center;
}

.wrapper {
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content {
    margin: auto;
    text-align: center;
}

.flex {
    display: flex;
    justify-content: space-around;
}